@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&family=Nerko+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&family=Nerko+One&display=swap);
body{
    margin: 0;
    background-color: #181616;
    /* background-color: #e0dede; */
    /* background-color: black; */
}
.main{
    width: 80vw;
    /* background-color: #f2f2f2; */
    /* background-color: black; */
    /* background-color: #F76B1C; */
    margin: 0 auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    position: relative;
}

.section{
    background-color: #ffd2b8;
    width: 100%;
    margin-top: 5vh;
    color: rgba(0, 0, 0, 0.6);
}

h1, h3 {
    color: rgba(0, 0, 0, 0.6);
}
a{
    /* text-decoration: none; */
    color: currentColor;
}


.navbar{
    background-color: rgba(0, 0, 0,1);
    /* background-color:#101010; */
    color: white;
    height: 7vh;
    width: 100vw;
    margin-top: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
}

.navbar .navlinks{
    color: whitesmoke;
    padding-right: 2vw;
    font-size: 1.7vw;
    text-decoration: none;
}

.home{
    /* background-color: green; */
    margin: 0 auto;
    width: 100%;
    height: 85vh;
    background: linear-gradient(-45deg, #F2D50F, #F76B1C,#DA0641,#FAD961);
	background-size: 400% 400%;
    animation: gradient 12s ease infinite;
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    align-items: center;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.title{
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-size: larger;
}
.pic{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    padding-top: 15px;
}

/* carousel */
.carousel{
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 20vh;
    height: 20vh;


}


.home .slide {
    width: 200px;
    height: 200px;
}

.carousel .icon{
    width: 60px;
}
.carousel .icon:hover{
    transform: scale(1.2);
}

.about-me{
    padding-top: 10vh;
    height: 80vh;
    display:flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
}

.container{
    margin-top: 5vh;
    /* background-color: green; */
    width: 70vw;
    height: 70vh;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.about-me_pic{
    width: 25vw;
    height: 50vh;
}

.about-me_description{
    width: 20vw;
    height: 20vh;
}

.contact{
    display: flex;
    flex-direction: column;
    height: 70vh;
    /* justify-content: space-between; */
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    padding-bottom: 15vh;
}

.contact-form{
    display:flex;
    flex-direction: row;
    margin: 3vw;
    /* background-color: red; */
    justify-content: space-between;
}

.contact-form .info{
    display: flex;
    flex-direction: column;
    height: 30vh;
    justify-content: space-around;
    /* background-color: green; */
}
.contact form{
    /* background-color: yellow; */
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
input, textarea {
    background: none;
    border: none;
    border-bottom: 2px #f8ba96 solid;
    width: 300px;
    padding-top: 50px;
    outline: none;
    font-size: large;
    color: rgba(0, 0, 0, 0.5);
}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder{
    font-family: 'Montserrat', sans-serif;
    font-size: large;
    color: rgba(0, 0, 0, 0.3);
}
input:-ms-input-placeholder, textarea:-ms-input-placeholder{
    font-family: 'Montserrat', sans-serif;
    font-size: large;
    color: rgba(0, 0, 0, 0.3);
}
input::-ms-input-placeholder, textarea::-ms-input-placeholder{
    font-family: 'Montserrat', sans-serif;
    font-size: large;
    color: rgba(0, 0, 0, 0.3);
}
input::placeholder,textarea::placeholder{
    font-family: 'Montserrat', sans-serif;
    font-size: large;
    color: rgba(0, 0, 0, 0.3);
}
textarea{
    width: 700px;
}

.row{
    width: 700px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

button{
    width: 90px;
    height: 50px;
    background: #F76B1C;
    border-radius: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin-top: 2vh;
    border: none;
    color: white;
}
button:hover{
    transform: scale(1.03);
    cursor: pointer;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

.column{
    display: flex;
    flex-direction: column;
}
.icon img{
    width: 3vw;
}

.projects{
    display: flex;
    flex-direction: column;
    height: 70vh;
    /* justify-content: space-between; */
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    padding-bottom: 15vh;
    color: rgba(0, 0, 0, 0.6);
}

.projects-container{
    width: 80vw;
    margin-top: 10vh;
    /* background-color: greenyellow; */
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: baseline;
}

.projects-container div{
    width: 17vw;
    height: 30vh;
}


.cards {
    display: flex;
    flex-direction: column;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.cards .card_img{
    width: 18vw;
    height: 30vh;
    border-radius: 5px;

}

.cards:hover{
    transform: scale(1.1);
    cursor: pointer;
}

.modal {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: 'Montserrat', sans-serif;
}

.modal_images {
    display: flex;
    flex-direction: row;
    overflow: hidden;
    flex-wrap: wrap;
    width: 40vw;
    margin-left: 100px;
}

.modal_img{
    width: 13vw;
    height: 20vh;
    padding: 0.5vw;
}

.modal_img:hover{
    transform: scale(1.05);
}

#links a{
    padding: 40px;
    text-decoration: none;
    margin-top: 20vh;
    /* overflow: hidden; */
    color: black;
    font-size: 20px;
}
#links a:hover{
    color: #F76B1C;
    font-size: 21px;
}

#tech {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    padding-top: 20px;
}
#tech h4{
    margin-bottom: 0;
}
#tech-items{
    display: flex;
    flex-direction: row;
}


.item{
    padding: 0.5vw;
}

#details{
    text-align: center;
    margin-right: 2vw;
    margin-left: 2vw;
}

.skills{
    padding-top: 10vh;
    height: 80vh;
    display:flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Montserrat', sans-serif;
    color: rgba(0, 0, 0, 0.7);
}

.skills .container {
    /* padding-top: 10vh;
    height: 80vh; */
    display:flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    font-family: 'Montserrat', sans-serif;
}

.skills .new-skill{
    width: 300px;
    height: 200px;
    /* background-color: green; */
    display: flex;
    flex-direction: column;
    text-align: center;
}

.new-skill img{
    margin: 0 auto;
    width: 100px;
}

